/* App.css */
.app {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.app .header {
  text-align: center;
  font-size: 1rem;
  & button {
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    background-color: goldenrod;
    color: white;
    font-weight: bold;
    &:hover {
      background-color: black;
      color: white;
    }
  }
}